////// Packages //////////////////
import React, {FC} from 'react';
import classNames from 'classnames';
import { Link } from 'react-router-dom';

////// Local Components //////////

////// Global Components /////////

////// CommonJS Helpers //////////

////// Custom Hooks //////////////

////// Thunks ////////////////////

//////// Assets ////////
import PinLogo from 'components/PinLogo';

interface Props {
  at?: 'navbar-vertical' | 'navbar-top' | 'auth';
  width: number;
  className?: string;
  horiz?: boolean;
  pinStrokeRGB?: {r: number; g: number; b: number;};
}

const Logo: FC<Props> = ({ at, width, className, horiz, pinStrokeRGB, ...rest }) => {
  return (
    <Link
      to="/home"
      className={classNames(
        'text-decoration-none',
        { 'navbar-brand text-left': at === 'navbar-vertical' },
        { 'navbar-brand text-left': at === 'navbar-top' }
      )}
      {...rest}
    >
      <div
        className={classNames(
          'd-flex',
          {
            'align-items-center py-3': at === 'navbar-vertical',
            'align-items-center': at === 'navbar-top',
            'flex-center font-weight-normal fs-5 mb-4': at === 'auth'
          },
          className
        )}
      >
        {/* <img className="mr-2" src={horiz ? logo_horiz : logo} alt="Logo" width={width} />*/}

        <PinLogo horiz={horiz} width={width} pinStrokeRGB={pinStrokeRGB} className="pr-1" />

        <span className="text-sans-serif">happin</span>
      </div>
    </Link>
  );
};

Logo.defaultProps = { at: 'auth', width: 30, horiz: false };

export default Logo;
