import React, { FC } from 'react';
import Lottie from 'react-lottie';
import animationData_vert from 'assets/img/logos/pinAnimation.json';
import animationData_horiz from 'assets/img/logos/pinAnimation_side.json';

// const aspectRatio = 285.58 / 178.65;

interface Props {
    width: number;
    horiz?: boolean;
    className?: string;
    pinStrokeRGB?: {r: number; g: number; b: number;}
}

const PinLogo: FC<Props> = ({width, horiz, className, pinStrokeRGB = {r: 44, g: 123, b: 229}}) => {
    
    let animationData;
    if (horiz) {
        animationData = animationData_horiz;
    } else {
        animationData = animationData_vert;
    }

    const _r = pinStrokeRGB.r / 255;
    const _g = pinStrokeRGB.g / 255;
    const _b = pinStrokeRGB.b / 255;

    const arrColors = animationData.layers[0].shapes[0].it![1].c!.k;
    arrColors[0] = _r;
    arrColors[1] = _g;
    arrColors[2] = _b;

    

    const defaultOptions = {
        loop: false,
        autoplay: true,
        animationData,
        rendererSettings: {
            preserveAspectRatio: 'xMidYMid slice'
        }
    };

    return <Lottie options={defaultOptions} style={{ width, marginRight: horiz ? width/9 : width/3, marginLeft: 0 }} />

};

export default PinLogo;