import React, { FC } from 'react';
import { Form, Input, InputProps } from 'reactstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

const SearchBox: FC<InputProps> = ({type, placeholder, className, ...props}) => (
  <Form inline className="search-box">
    <Input type="search" placeholder="Search..." aria-label="Search" className={"rounded-pill search-input " + className} {...props}/>
    <FontAwesomeIcon icon="search" className="position-absolute text-400 search-box-icon" />
  </Form>
);

export default SearchBox;
