////// Packages //////////////////
import React, { useCallback, useState, FC } from "react";

////// Local Components //////////

////// Global Components /////////

////// CommonJS Helpers //////////
import getSizedImageName from "../helpers/getSizedImageName";
////// Custom Hooks //////////////

////// Thunks ////////////////////

export interface Props extends React.ImgHTMLAttributes<HTMLImageElement> {
  size?: "s" | "m" | "l" | "original";
}

const Image: FC<Props> = ({
  size = "original",
  src = "",
  alt = "",
  onLoad,
  className = "",
  width = "auto",
  height = "auto",
  ...imageProps
}) => {
  const [isImageLoaded, setIsImageLoaded] = useState(false);
  const handleLoad = useCallback(() => setIsImageLoaded(true), []);

  return (
    <>
      <img
        className={[className, isImageLoaded ? "d-block" : "d-none"].join(" ")}
        src={getSizedImageName(src, size)}
        alt={alt}
        onLoad={handleLoad}
        width={width}
        height={height}
        {...imageProps}
      />
      {!isImageLoaded && (
        <div style={{ width, height }} className="image-placeholder" />
      )}
    </>
  );
};

export default Image;
