import { has } from "lodash";
import deduplicator from 'store/ducks/deduplicator';
import { ThunkAction } from "redux-thunk";
import { DefaultRootState } from "react-redux";

function deduplicate (testValue: (string|number)[], cb: ()=>void, force?: boolean):
    ThunkAction<void | null, DefaultRootState, any, any> {
        return (dispatch, getState) => {
            if (!force) {
                const state = getState();
                if ( has( state.deduplicator, testValue ) ) return null;
            }
            dispatch( deduplicator.actions.set(testValue) )
            return cb()
        }
}

export default deduplicate;