export const types = {
    SET: 'happin/deduplicator/SET',
    CLEAR: 'happin/deduplicator/CLEAR',
    RESET: 'happin/deduplicator/RESET'
}

export function reducer(state = {}, action = {}) {
    switch(action.type) {
        case types.SET: {
            const obj = {...state};
            let recursor = obj;
            for (const i in action.payload) {
                const key = action.payload[i];
                if (i < action.payload.length - 1) {
                    recursor[key] = {...recursor[key]}
                    recursor = recursor[key]
                } else {
                    recursor[key] = true;
                }
            }
            return obj;
        }

        case types.CLEAR: {
            const obj = {...state};
            let recursor = obj;
            for (const i in action.payload) {
                const key = action.payload[i];
                if (i < action.payload.length - 1) {
                    recursor[key] = {...recursor[key]}
                    recursor = recursor[key]
                } else {
                    delete recursor[key];
                }
            }
            return obj;
        }

        case types.RESET: return {};

        default: return state;
    }
}

function set(path) {
    if (!Array.isArray(path)) return {type: ''}
    return {
        type: types.SET,
        payload: path
    }
}

function clear(path) {
    if (!Array.isArray(path)) return {type: ''}
    return {
        type: types.CLEAR,
        payload: path
    }
}

function reset() {
    return {
        type: types.RESET
    }
}

export const actions = {
    set,
    clear,
    reset
}
const deduplicator = {
    types,
    reducer,
    actions
}

export default deduplicator;