//===============================
// Echarts
//===============================
export const getPosition = <T extends {contentSize: [number, number]}>(pos: [number, number], params: any, dom: Document, rect: any, size: T) => ({
  top: pos[1] - size.contentSize[1] - 10,
  left: pos[0] - size.contentSize[0] / 2
});

//===============================
// E-Commerce
//===============================
export const calculateSale = (base: number, less = 0, fix = 2) => (base - base * (less / 100)).toFixed(fix);
export const getTotalPrice = (cart: {id: any, quantity: number}[], baseItems: { id: any, price: number, sale: number }[] ) =>
  cart.reduce((accumulator, currentValue) => {
    const { id, quantity } = currentValue;
    const { price, sale } = baseItems.find(item => item.id === id) || {};
    return accumulator + +calculateSale(price || NaN, sale) * quantity;
  }, 0);

//===============================
// Helpers
//===============================
export const getPaginationArray = (totalSize: number, sizePerPage: number) => {
  const noOfPages = Math.ceil(totalSize / sizePerPage);
  const array = [];
  let pageNo = 1;
  while (pageNo <= noOfPages) {
    array.push(pageNo);
    pageNo = pageNo + 1;
  }
  return array;
};

export const capitalize = (str: string) => (str.charAt(0).toUpperCase() + str.slice(1)).replace(/-/g, ' ');

interface SlicerArgs {
  routes: RouteDef[];
  columns?: number;
  rows?: number;
}
export const routesSlicer = ({ routes, columns = 3, rows }: SlicerArgs) => {
  const routesCollection = [] as RouteDef[];
  routes.map(route => {
    if (route.children) {
      return route.children.map(item => {
        if (item.children) {
          return routesCollection.push(...item.children);
        }
        return routesCollection.push(item);
      });
    }
    return routesCollection.push(route);
  });

  const totalRoutes = routesCollection.length;
  const calculatedRows = rows || Math.ceil(totalRoutes / columns);
  const routesChunks = [];
  for (let i = 0; i < totalRoutes; i += calculatedRows) {
    routesChunks.push(routesCollection.slice(i, i + calculatedRows));
  }
  return routesChunks;
};

export const getPageName = (pageName: string) => {
  return window.location.pathname.split('/').slice(-1)[0] === pageName;
};

export const copyToClipBoard = <T extends HTMLInputElement>(textFieldRef: RefObject<T>) => {
  const textField = textFieldRef.current;
  if (!textField) return;
  textField.focus();
  textField.select();
  document.execCommand('copy');
};
