import React, { FC } from "react";
import classNames from "classnames";

interface Props {
  justify?: "start" | "center" | "end" | "between" | "around";
  align?: "start" | "center" | "end";
  inline?: boolean;
  column?: boolean;
  wrap?: boolean;
  className?: string;
  tag?: any;
  children?: React.ReactNode;
  id?: string;
  onClick?: (...args: any[]) => void;
}

const Flex: FC<Props> = ({
  justify,
  align,
  inline,
  column,
  wrap,
  className,
  tag: Tag = "div",
  children,
  id,
  ...rest
}) => {
  return (
    <Tag
      className={classNames(
        {
          "d-flex": !inline,
          "d-inline-flex": inline,
          [`justify-content-${justify}`]: justify,
          [`align-items-${align}`]: align,
          "flex-column": column,
          "flex-wrap": wrap
        },
        className
      )}
      {...rest}
    >
      {children}
    </Tag>
  );
};

export default Flex;
