import { 
    MWorkflow,
    MStage,
    MThread,
    MTask
} from "store/types/models/workflow";
import { BaseDuck } from "./BaseDuck";
import { combineReducers } from 'redux';


export const workflows = new BaseDuck<MWorkflow>('workflows');

export const stages = new BaseDuck<MStage>('stages');
stages.setKeys = ['tasks', 'threads', 'tags'];
stages.replaceKeys = ['coordinates'];

export const tasks = new BaseDuck<MTask>('tasks');
tasks.setKeys = [];

export const threads = new BaseDuck<MThread>('threads');
threads.setKeys = ['members'];

export const workflowReducer = combineReducers({
    workflows: workflows.reducer.bind(workflows),
    stages: stages.reducer.bind(stages),
    tasks: tasks.reducer.bind(tasks),
    threads: threads.reducer.bind(threads)
})